body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-link {
	font-family: 'Open Sans', sans-serif;
	font-size: 16pt;
	margin-bottom: 2px;
	padding-left: 12px !important;
	padding-right: 12px !important;
}

li > a {
	color: rgba(255,255,255,.5) !important;
}

li > a:hover, li > a:active, li > .nav-item:hover {
	color: #eeeeee !important;
	margin-bottom: 0px;
	border-bottom: 2px solid #eeeeee;
}

.active {
	margin-bottom: 0px;
	border-bottom: 2px solid #eeeeee;
	color: #eeeeee !important;
}

.nav-brand {
	font-family: 'Open Sans', sans-serif;
	font-size: 16pt;
}
.centerAlign {
	align-items: center;
	justify-content: center;
	height: 100%;
	position: relative;
}
.projectContainer {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.page {
	height: 100vh;
	width: 100vw; 
	background-color: white;
	display: flex;
	flex-direction: column;
}

.headingBar {
	width: 100vw;
	height: 20vh;
	background-color: #fffbf2;
	display: flex;
}

.body {
	width: 100vw;
	background-color: white;
	display: flex;
	flex-direction: column;
	margin: 3vh 5vw 3vh 5vw;
}

.heading {
	font-family: 'Montserrat', sans-serif;
	font-weight: bold;
	font-size: 30pt;
	align-items: center;
	display: flex;
	padding-left: 5vw;
}

.leftColumn {
	width: 46%;
	margin: 1rem 2% 0 0;
}

.rightColumn {
	width: 41%;
	margin: 1rem 0 0 0;
	text-align: center;
}

.card {
	border-style: solid;
	border-color: #d3d3d3;
	-webkit-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out;
}

.card:hover {
	box-shadow: 1px 1px 3px 2px #eeeeee;
	-webkit-transform: scale(1.03);
	        transform: scale(1.03);
}

.card-header {
	background-color: white;
}

.grow { 
	-webkit-transition: all .2s ease-in-out; 
	transition: all .2s ease-in-out; 
}

.grow:hover { 
	-webkit-transform: scale(1.1); 
	        transform: scale(1.1); 
}

h1 {
	font-family: 'Montserrat', sans-serif;
	font-weight: bolder;
	font-size: 40px;
}

h2 {
	font-family: 'Open Sans', sans-serif;
	font-size: 25pt;
}

h3 {
	font-family: 'Open Sans', sans-serif;
	font-size: 25pt;
	font-weight: bold;
	margin-bottom: 3vh;
}

p {
	font-family: 'Open Sans', sans-serif;
	font-size: 18pt;
}

a {
	font-family: 'Open Sans', sans-serif;
}

a:hover {
	cursor: pointer;
}

html {
	scroll-behavior: smooth;
}
